import {Typography} from "@mui/material";


export const Text = () => {
    return (
        <>
            <Typography
                variant="body1"
                // component="pre"
                align="center"
                style={{
                    paddingTop: '3ch',
                    whiteSpace: 'pre-line',
                    fontFamily: 'Secular One',
                    fontSize: '2vw',
                }}
            >
                {/*{"  '  !\n"}*/}
                {/*{"  ! \n"}*/}
                {"  2022\n"}
                {" ..  ?\n"}
                {"    !\n"}
                {" ,   \n"}
                {"         !\n"}
                {"?         !!!        \n"}
                {"  ..              ,  \n"}
                {"         \n"}
                {".    !\n"}
                {".   !\n"}

                {"   ,        !          \n"}
                {"   \n"}
                {"...     ?\n"}

            </Typography>
        </>
    );
};
